}

.header h1{
    font-size: 36px;
   padding: 0.8rem;
    background-color: #000;
    color:#fff;
    border-radius: 10px;
}



.main-image {
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;
}

.main-image img {
    width: auto;
    height: 100%;
    object-fit: cover;
    transform: rotateZ(-7deg) translateY(-2rem) translateX(5rem);
}

@media(max-width: 900px){
    .header{
        height: 6rem;
    }
    .header h1{

    }
}

@media(max-width: 600px){
    .header{
        height: 6rem;
        background-size: 50%;
    }
    .header h1{
        padding: 0.6rem;
        font-size: 28px;
    }
}