.cart-items {
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 20rem;
    overflow: auto;
}

.total {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-size: 1.5rem;
    margin: 1rem 0;
}

.actions {
    text-align: right;
}

.actions button {
    font: inherit;
    cursor: pointer;
    background-color: transparent;
    border: 1px solid #000;
    padding: 0.5rem 2rem;
    border-radius: 25px;
    margin-left: 1rem;
    transition: all .2s linear;
}

.actions .button:hover,
.actions .button:active {
    background-color:#E15B8C;
    border-color: #E15B8C;
    color: white;
}

.actions .button--alt {

}
.button--alt:hover, .actions .button--alt:active{
    background-color: #000;
    color:#fff;
}
.actions .button {
    background-color: #D87F5B;
    border-color: #D87F5B;
    color: white;
}